<script>
import { mapState } from 'vuex'
import moment from 'moment';
import Datepicker from 'vuejs-datepicker';
import firebase from "@/firebaseConfig";
const db = firebase.firestore();
const storage = firebase.storage();
const storageRef = storage.ref();

let current = 1;
import ListPlayer from "./../components/ListPlayer";
// import ProgressBar from "./../components/ProgressBar";
export default {
  components: {
    ListPlayer,
    Datepicker
    // ProgressBar
  },
  data: () => ({
    playerr: {
      name: '',
      lastName: '',
      telephone: '',
      birthDate: '',
      birthplace: '',
      residence: '',
      educationLevel: '',
      profession: '',
      height: '',
      foot: '',
      position: '',
      dream: '',
      photo: '',
      team: ''
    },
    playerrReset: {
      name: '',
      lastName: '',
      telephone: '',
      birthDate: '',
      birthplace: '',
      residence: '',
      educationLevel: '',
      profession: '',
      height: '',
      foot: '',
      position: '',
      dream: '',
      photo: '',
      team: ''
    },
    loading: false
  }),
  computed: {
    ...mapState(['team'])
  },
  created () {
  },
  methods: {
    loginBtn (e) {
      console.log(e);
      const signupBtn = document.getElementById('signup');
      let parent = e.target.parentNode.parentNode;
      Array.from(e.target.parentNode.parentNode.classList).find((element) => {
        if (element !== "slide-up") {
          parent.classList.add('slide-up')
        } else {
          signupBtn.parentNode.classList.add('slide-up')
          parent.classList.remove('slide-up')
        }
      });
    },

    signupBtn (e) {
      console.log(e);
      const loginBtn = document.getElementById('login');
      let parent = e.target.parentNode;
      Array.from(e.target.parentNode.classList).find((element) => {
        if (element !== "slide-up") {
          parent.classList.add('slide-up')
        } else {
          loginBtn.parentNode.parentNode.classList.add('slide-up')
          parent.classList.remove('slide-up')
        }
      });
    },
    nextBtnFirst (event) {
      event.preventDefault();
      const slidePage = this.$el.querySelector(".slide-page");
      const progressText = this.$el.querySelectorAll(".step p");
      const progressCheck = this.$el.querySelectorAll(".step .check");
      const bullet = this.$el.querySelectorAll(".step .bullet");

      slidePage.style.marginLeft = "-25%";
      bullet[current - 1].classList.add("active");
      progressCheck[current - 1].classList.add("active");
      progressText[current - 1].classList.add("active");
      current += 1;
    },
    nextBtnSec (event) {
      event.preventDefault();
      const slidePage = this.$el.querySelector(".slide-page");
      const progressText = this.$el.querySelectorAll(".step p");
      const progressCheck = this.$el.querySelectorAll(".step .check");
      const bullet = this.$el.querySelectorAll(".step .bullet");

      slidePage.style.marginLeft = "-50%";
      bullet[current - 1].classList.add("active");
      progressCheck[current - 1].classList.add("active");
      progressText[current - 1].classList.add("active");
      current += 1;
    },
    nextBtnThird (event) {
      event.preventDefault();
      const slidePage = this.$el.querySelector(".slide-page");
      const progressText = this.$el.querySelectorAll(".step p");
      const progressCheck = this.$el.querySelectorAll(".step .check");
      const bullet = this.$el.querySelectorAll(".step .bullet");

      slidePage.style.marginLeft = "-75%";
      bullet[current - 1].classList.add("active");
      progressCheck[current - 1].classList.add("active");
      progressText[current - 1].classList.add("active");
      current += 1;
    },
    submitBtn (event) {
      event.preventDefault();
      this.addPlayer();

    },
    prevBtnSec (event) {
      event.preventDefault();
      const slidePage = this.$el.querySelector(".slide-page");
      const progressText = this.$el.querySelectorAll(".step p");
      const progressCheck = this.$el.querySelectorAll(".step .check");
      const bullet = this.$el.querySelectorAll(".step .bullet");

      slidePage.style.marginLeft = "0%";
      bullet[current - 2].classList.remove("active");
      progressCheck[current - 2].classList.remove("active");
      progressText[current - 2].classList.remove("active");
      current -= 1;
    },
    prevBtnThird (event) {
      event.preventDefault();
      const slidePage = this.$el.querySelector(".slide-page");
      const progressText = this.$el.querySelectorAll(".step p");
      const progressCheck = this.$el.querySelectorAll(".step .check");
      const bullet = this.$el.querySelectorAll(".step .bullet");

      slidePage.style.marginLeft = "-25%";
      bullet[current - 2].classList.remove("active");
      progressCheck[current - 2].classList.remove("active");
      progressText[current - 2].classList.remove("active");
      current -= 1;
    },
    prevBtnFourth (event) {
      event.preventDefault();
      const slidePage = this.$el.querySelector(".slide-page");
      const progressText = this.$el.querySelectorAll(".step p");
      const progressCheck = this.$el.querySelectorAll(".step .check");
      const bullet = this.$el.querySelectorAll(".step .bullet");

      slidePage.style.marginLeft = "-50%";
      bullet[current - 2].classList.remove("active");
      progressCheck[current - 2].classList.remove("active");
      progressText[current - 2].classList.remove("active");
      current -= 1;
    },
    fixDate (event) {
      return moment(event).format('YYYY-MM-DD');
    },
    addPlayer () {
      this.loading = true;
      const slidePage = this.$el.querySelector(".slide-page");
      const progressText = this.$el.querySelectorAll(".step p");
      const progressCheck = this.$el.querySelectorAll(".step .check");
      const bullet = this.$el.querySelectorAll(".step .bullet");

      const myInput = this.$el.querySelector('#photo');
      var file = myInput.files[0];
      console.log(file);

      const splitPhotoName = file.name.split(".");
      const photoExtension = splitPhotoName[splitPhotoName.length - 1];
      const photoName = this.playerr.name + '' + this.playerr.lastName + '.' + photoExtension;

      var uploadTask = storageRef.child(`wplayers/${this.team}/${photoName}`).put(file);
      var uploader = this.$el.querySelector("#uploader");
      this.playerr.team = this.team;
      this.playerr.birthDate = moment(this.playerr.birthDate).format('L');
      this.playerr.birthDate = moment(this.playerr.birthDate).format('L');
      const vm = this;

      uploadTask.on('state_changed', function (snapshot) {
        // Observe state change events such as progress, pause, and resume
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload is ' + progress + '% done');
        uploader.value = progress;
        // switch (snapshot.state) {
        //   case firebase.storage.TaskState.PAUSED: // or 'paused'
        //     console.log('Upload is paused');
        //     break;
        //   case firebase.storage.TaskState.RUNNING: // or 'running'
        //     console.log('Upload is running');
        //     break;
        // }
      }, function (error) {
        console.error(error);
      }, function () {
        // Handle successful uploads on complete
        // For instance, get the download URL: https://firebasestorage.googleapis.com/...
        uploadTask.snapshot.ref.getDownloadURL().then(function (downloadURL) {
          vm.playerr.photo = downloadURL;
          console.log('File available at', downloadURL);
          db.collection("wplayers").add(vm.playerr)
            .then(function (docRef) {
              vm.playerr = vm.playerrReset;
              vm.loading = false;
              console.log("Document written with ID: ", docRef.id);
              // eslint-disable-next-line no-undef
              Fire.$emit('loadPlayers');
              slidePage.style.marginLeft = "0%";
              bullet[0].classList.remove("active");
              progressCheck[0].classList.remove("active");
              progressText[0].classList.remove("active");
              bullet[1].classList.remove("active");
              progressCheck[1].classList.remove("active");
              progressText[1].classList.remove("active");
              bullet[2].classList.remove("active");
              progressCheck[2].classList.remove("active");
              progressText[2].classList.remove("active");
              bullet[3].classList.remove("active");
              progressCheck[3].classList.remove("active");
              progressText[3].classList.remove("active");
              current = 1;
              uploader.value = 0;
            })
            .catch(function (error) {
              console.error("Error adding document: ", error);
            });
        });
      });
    }
  },
  mounted () {
    if (!this.team) {
      this.$router.push(this.$route.query.redirect || '/login')
    }
  }
}
</script>

