<template>
  <div class="list-player">
    <div
      class="player"
      v-for="(player, index) in players"
      :key="index"
    >
      <div class="photo">
        <img
          :src="player.photo"
          alt="Avatar"
        >
      </div>
      <div class="info">
        <p><span>Nome: </span>{{player.name}}</p>
        <p><span>Number: </span>{{player.daamisolaNumber}}</p>
      </div>
      <div class="info">
        <p><span>Posio: </span>{{player.position}}</p>
        <p><span>P: </span>{{player.foot}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import firebase from "@/firebaseConfig";
const db = firebase.firestore();
export default {
  data: () => ({
    players: [],
  }),
  computed: {
    ...mapState(['team']),
    playerrs () {
      return this.players
    }
  },
  created () {
    this.showPlayers();
    // eslint-disable-next-line no-undef
    Fire.$on('loadPlayers', () => {
      this.showPlayers();
    });
  },
  methods: {
    showPlayers () {
      this.players = []
      const vm = this;

      db.collection("wplayers").where("team", "==", this.team)
        .get()
        .then(function (querySnapshot) {
          querySnapshot.forEach(function (doc) {
            vm.players.push(doc.data())
          });
        })
        .catch(function (error) {
          console.log("Error getting documents: ", error);
        });
    },
  }
}
</script>

